// utils/web3.js
import Web3 from 'web3';

// é…ç½®å¸¸é‡
const SEPOLIA_CHAIN_ID = 11155111; // åè¿›åˆ¶
const SEPOLIA_HEX_ID = '0xaa36a7'; // åå…­è¿›åˆ¶
const NETWORK_SWITCH_TIMEOUT = 3000; // ç½‘ç»œåˆ‡æ¢è¶…æ—¶3ç§’
const ACCOUNT_REQUEST_TIMEOUT = 5000; // è´¦æˆ·è¯·æ±‚è¶…æ—¶5ç§’

let web3;

// åˆå§‹åŒ–Web3ï¼ˆéé˜»å¡å¼ï¼‰
const initWeb3 = () => {
    if (typeof window === 'undefined') return null;

    try {
        if (window.ethereum) {
            web3 = new Web3(window.ethereum);
            setupNetworkListeners();
            console.debug('[Web3] å®ä¾‹åˆå§‹åŒ–æˆåŠŸ');
            return web3;
        }
        console.error('[Web3] æœªæ£€æµ‹åˆ°ä»¥å¤ªåŠæä¾›å•†');
        return null;
    } catch (error) {
        console.error('[Web3] åˆå§‹åŒ–å¤±è´¥:', error);
        return null;
    }
};

// ç½‘ç»œäº‹ä»¶ç›‘å¬å™¨
const setupNetworkListeners = () => {
    window.ethereum?.on('chainChanged', handleChainChanged);
    window.ethereum?.on('accountsChanged', handleAccountsChanged);
};

const handleChainChanged = (chainId) => {
    const decimalChainId = parseInt(chainId, 16);
    console.debug('[Web3] ç½‘ç»œå˜æ›´:', decimalChainId);
    // å¯åœ¨æ­¤å¤„è§¦å‘åº”ç”¨çŠ¶æ€æ›´æ–°
};

const handleAccountsChanged = (accounts) => {
    console.debug('[Web3] è´¦æˆ·å˜æ›´:', accounts);
    // å¯åœ¨æ­¤å¤„è§¦å‘åº”ç”¨çŠ¶æ€æ›´æ–°
};

// æ ¸å¿ƒè¿æ¥æµç¨‹
const connectWallet = async () => {
    if (!web3) throw new Error('Web3æœªåˆå§‹åŒ–');

    try {
        // é˜¶æ®µ1ï¼šç½‘ç»œæ£€æŸ¥ä¸åˆ‡æ¢
        const currentChainId = await getChainIdWithTimeout();
        if (currentChainId !== SEPOLIA_CHAIN_ID) {
            await handleNetworkSwitch();
        }

        // é˜¶æ®µ2ï¼šè´¦æˆ·è¯·æ±‚
        const account = await requestAccountsWithTimeout();
        return account;
    } catch (error) {
        console.error('[Web3] è¿æ¥å…¨æµç¨‹é”™è¯¯:', error);
        throw normalizeError(error);
    }
};

// å¸¦è¶…æ—¶çš„é“¾IDè·å–
const getChainIdWithTimeout = () => {
    return Promise.race([
        web3.eth.getChainId(),
        new Promise((_, reject) =>
            setTimeout(() => reject(new Error('ç½‘ç»œçŠ¶æ€è¯·æ±‚è¶…æ—¶')), NETWORK_SWITCH_TIMEOUT)
        )
    ]);
};

// ç½‘ç»œåˆ‡æ¢å¤„ç†
const handleNetworkSwitch = async () => {
    try {
        await Promise.race([
            window.ethereum.request({
                method: 'wallet_switchEthereumChain',
                params: [{ chainId: SEPOLIA_HEX_ID }]
            }),
            new Promise((_, reject) =>
                setTimeout(() => reject(new Error('ç½‘ç»œåˆ‡æ¢è¶…æ—¶')), NETWORK_SWITCH_TIMEOUT)
            )
        ]);
    } catch (switchError) {
        if (switchError.code === 4902) {
            await addSepoliaNetwork();
        } else {
            throw new Error(switchError.message || 'ç½‘ç»œåˆ‡æ¢è¢«æ‹’ç»');
        }
    }
};

// æ·»åŠ Sepoliaç½‘ç»œ
const addSepoliaNetwork = async () => {
    try {
        await window.ethereum.request({
            method: 'wallet_addEthereumChain',
            params: [{
                chainId: SEPOLIA_HEX_ID,
                chainName: 'Sepolia Test Network',
                nativeCurrency: {
                    name: 'Sepolia ETH',
                    symbol: 'ETH',
                    decimals: 18
                },
                rpcUrls: ['https://rpc.sepolia.org'],
                blockExplorerUrls: ['https://sepolia.etherscan.io']
            }]
        });
    } catch (addError) {
        throw new Error(addError.message || 'æ·»åŠ ç½‘ç»œå¤±è´¥');
    }
};

// å¸¦è¶…æ—¶çš„è´¦æˆ·è¯·æ±‚
const requestAccountsWithTimeout = async () => {
    const accounts = await Promise.race([
        window.ethereum.request({ method: 'eth_requestAccounts' }),
        new Promise((_, reject) =>
            setTimeout(() => reject(new Error('è´¦æˆ·è¯·æ±‚è¶…æ—¶')), ACCOUNT_REQUEST_TIMEOUT)
        )
    ]);

    if (!accounts?.[0]) throw new Error('æœªè·å–åˆ°æœ‰æ•ˆè´¦æˆ·');
    return accounts[0];
};

// é”™è¯¯æ ‡å‡†åŒ–å¤„ç†
const normalizeError = (error) => {
    const errorMap = {
        'User rejected the request': 'ç”¨æˆ·å–æ¶ˆæ“ä½œ',
        'wallet_addEthereumChain': 'ç”¨æˆ·æ‹’ç»æ·»åŠ ç½‘ç»œ',
        'wallet_switchEthereumChain': 'ç”¨æˆ·æ‹’ç»åˆ‡æ¢ç½‘ç»œ'
    };

    return new Error(errorMap[error?.message] || error.message || 'æœªçŸ¥é”™è¯¯');
};

// è·å–å½“å‰è´¦æˆ·ï¼ˆå…¼å®¹åŸæœ‰ä»£ç ï¼‰
const getAccount = async () => {
    try {
        const accounts = await web3.eth.getAccounts();
        return accounts[0] || null;
    } catch (error) {
        console.error('[Web3] è·å–è´¦æˆ·å¤±è´¥:', error);
        return null;
    }
};

// ç½‘ç»œå˜æ›´ç›‘å¬ï¼ˆå…¼å®¹åŸæœ‰ä»£ç ï¼‰
const listenNetworkChange = (callback) => {
    window.ethereum?.on('chainChanged', (hexChainId) => {
        callback(parseInt(hexChainId, 16));
    });
};

// å¯¼å‡ºéƒ¨åˆ†ä¿æŒå®Œå…¨å…¼å®¹
export const getWeb3 = () => {
    if (!web3) throw new Error('Web3æœªåˆå§‹åŒ–');
    return web3;
};

export {
    initWeb3,
    connectWallet,
    getAccount,
    listenNetworkChange,
    SEPOLIA_CHAIN_ID
};