const loadingStates = ref({
  mint: false,              // é“¸é€ ä»£å¸
  stake: false,             // è´¨æŠ¼æ“ä½œ
  unstake: false,           // è§£é™¤è´¨æŠ¼
  createProposal: false,    // åˆ›å»ºææ¡ˆ
  vote: new Set(),          // æŠ•ç¥¨æ“ä½œï¼ˆå­˜å‚¨ææ¡ˆIDï¼‰
  startProposal: new Set(),  // å¯åŠ¨ææ¡ˆï¼ˆå­˜å‚¨ææ¡ˆIDï¼‰
  closeProposal: new Set()  // å…³é—­ææ¡ˆåŠ è½½çŠ¶æ€
})

// ==================== é€šçŸ¥ç³»ç»Ÿ ====================
const notifications = ref([])
const typeIcons = {
  error: 'âŒ',
  success: 'âœ…',
  warning: 'âš ï¸',
  loading: 'â³' // æ–°å¢åŠ è½½çŠ¶æ€å›¾æ ‡
}

/**
 * æ˜¾ç¤ºé€šçŸ¥
 * @param {string} message - é€šçŸ¥å†…å®¹
 * @param {string} type - é€šçŸ¥ç±»å‹ï¼ˆerror/success/warning/loadingï¼‰
 * @param {boolean} persistent - æ˜¯å¦æŒä¹…åŒ–ï¼ˆä¸ä¼šè‡ªåŠ¨å…³é—­ï¼‰
 * @returns {number} é€šçŸ¥IDï¼ˆç”¨äºåç»­æ›´æ–°ï¼‰
 */
const showNotification = (message, type = 'error', persistent = false) => {
  const id = Date.now()
  notifications.value.push({
    id,
    message,
    type,
    persistent
  })



    setTimeout(() => {
      notifications.value = notifications.value.filter(n => n.id !== id)
    }, 5000)

  return id
}

// å¿«æ·æ–¹æ³•
const showError = (message) => showNotification(message, 'error')
// const showSuccess = (message) => showNotification(message, 'success')

/**
 * æ˜¾ç¤ºåŠ è½½ä¸­çš„é€šçŸ¥
 * @param {string} message - åŠ è½½æç¤ºä¿¡æ¯
 * @returns {number} é€šçŸ¥ID
 */
const showLoading = (message) => {
  return showNotification(message, 'loading', true)
}

// ==================== åˆå§‹åŒ– ====================

onMounted(async () => {

  try {
    await initWeb3()
    await initContracts()
    checkExistingConnection()
    listenNetworkChange(handleNetworkChange)
  } catch (err) {
    // showError('åˆå§‹åŒ–å¤±è´¥ï¼Œè¯·åˆ·æ–°é¡µé¢')
  }
})

onUnmounted(() => {
  clearInterval(timer.value)
})
// ==================== é’±åŒ…ç›¸å…³æ–¹æ³• ====================
const checkExistingConnection = async () => {
  try {
    const acc = await getAccount()
    if (acc) {
      account.value = acc
      await checkNetwork()
      await loadData()
    }
  } catch (err) {
    console.error('è‡ªåŠ¨è¿æ¥æ£€æŸ¥å¤±è´¥:', err)
  }
}

const handleConnect = async () => {
  console.log(1)
  try {
    console.log(2)

    account.value = await connectWallet()
    wrongNetwork.value = false
    await loadData()
  } catch (err) {
    console.log(3)
    showError('è¿æ¥å¤±è´¥: ' + (err.message || 'ç”¨æˆ·å–æ¶ˆæ“ä½œ'))
  }
}


// ä¿®æ”¹ç½‘ç»œæ£€æŸ¥æ–¹æ³•
const checkNetwork = async () => {
  try {
    const chainId = Number(await getWeb3().eth.getChainId()); // è½¬æ¢ä¸º number
    console.log('ç½‘ç»œæ£€æŸ¥:',
        `å½“å‰é“¾ID: ${chainId}ï¼ˆç±»å‹: ${typeof chainId}ï¼‰`,
        `ç›®æ ‡é“¾ID: ${SEPOLIA_CHAIN_ID}ï¼ˆç±»å‹: ${typeof SEPOLIA_CHAIN_ID}ï¼‰`
    );

    wrongNetwork.value = chainId !== SEPOLIA_CHAIN_ID;

    console.log('æ¯”è¾ƒç»“æœ:',
        wrongNetwork.value ? 'ç½‘ç»œé”™è¯¯' : 'ç½‘ç»œæ­£ç¡®',
        `è®¡ç®—å¼: ${chainId} !== ${SEPOLIA_CHAIN_ID}`
    );
  } catch {
    wrongNetwork.value = true;
  }
};

// ä¿®æ”¹ç½‘ç»œå˜æ›´å¤„ç†
const handleNetworkChange = (chainId) => {
  console.log('å¤„ç†ç½‘ç»œå˜æ›´:',
      `æ”¶åˆ°é“¾ID: ${chainId}ï¼ˆç±»å‹: ${typeof chainId}ï¼‰`,
      `ç›®æ ‡é“¾ID: ${SEPOLIA_CHAIN_ID}ï¼ˆç±»å‹: ${typeof SEPOLIA_CHAIN_ID}ï¼‰`
  );

  wrongNetwork.value = chainId !== SEPOLIA_CHAIN_ID;

  console.log('æ¯”è¾ƒç»“æœ:',
      wrongNetwork.value ? 'ç½‘ç»œé”™è¯¯' : 'ç½‘ç»œæ­£ç¡®',
      `è®¡ç®—å¼: ${chainId} !== ${SEPOLIA_CHAIN_ID}`
  );

  if (!wrongNetwork.value) loadData();
};


// ==================== æ•°æ®åŠ è½½ ====================
/**
 * åŠ è½½æ ¸å¿ƒæ•°æ®ï¼ˆä½™é¢ã€ææ¡ˆç­‰ï¼‰
 */
async function loadData() {
  if (wrongNetwork.value) return

  try {
    const tokenContract = getGovernanceTokenContract();
    const votingContract = getVotingContract();

    // åŠ è½½ä»£å¸ä¿¡æ¯
    tokenSymbol.value = await tokenContract.methods.symbol().call();
    const balance = await tokenContract.methods.balanceOf(account.value).call();
    myBalance.value = weiToEther(balance);

    // åŠ è½½è´¨æŠ¼ä¿¡æ¯
    const staked = await votingContract.methods.stakedTokens(account.value).call();
    stakedBalance.value = weiToEther(staked);

    // åŠ è½½ææ¡ˆ
    const rawProposals = await votingContract.methods.getAllProposals().call();
    proposals.value = await Promise.all(
        rawProposals.map(async (p, index) => {
          const currentTime = Math.floor(Date.now()/1000)
          const isOverdue = p.status === 1 && Number(p.endTime) < currentTime

          return {
            ...p,
            forWeight: weiToEther(p.forWeight),
            againstWeight: weiToEther(p.againstWeight),
            endTime: Number(p.endTime),
            status: isOverdue ? 2 : Number(p.status), // è‡ªåŠ¨æ›´æ–°è¿‡æœŸçŠ¶æ€
            hasVoted: await votingContract.methods.voters(index, account.value).call(),
            isPassed: p.status === 2 ? await votingContract.methods.isProposalPassed(index).call() : false,
            isOverdue
          }
        })

    );
  } catch (err) {
    // handleError(err, 'æ•°æ®åŠ è½½å¤±è´¥')
  }
}

// ==================== æ ¸å¿ƒåŠŸèƒ½ ====================
/**
 * é“¸é€ æµ‹è¯•ä»£å¸
 */
const networkGuard = () => {
  if (wrongNetwork.value) {
    showError('è¯·å…ˆåˆ‡æ¢åˆ°Sepoliaç½‘ç»œ')
    return true
  }
  return false
}

const mintTokens = async () => {
  if (networkGuard()) return
  const loadingId = showLoading('ä»£å¸é“¸é€ ä¸­...') // æ˜¾ç¤ºåŠ è½½æç¤º
  loadingStates.value.mint = true
  try {
    const tokenContract = getGovernanceTokenContract()
    const amount = 1000
    await tokenContract.methods.mint(
        getWeb3().utils.toWei(amount.toString(), 'ether')
    ).send({ from: account.value })

    await loadData()
    // æ›´æ–°é€šçŸ¥ä¸ºæˆåŠŸçŠ¶æ€
    notifications.value = notifications.value.map(n =>
        n.id === loadingId ? {
          ...n,
          message: `æˆåŠŸé¢†å– ${amount} ${tokenSymbol.value}`,
          type: 'success',
          persistent: false
        } : n
    )
  } catch (err) {
    notifications.value = notifications.value.filter(n => n.id !== loadingId)
    handleError(err, 'é¢†å–æµ‹è¯•å¸å¤±è´¥')
  } finally {
    loadingStates.value.mint = false
  }
}

/**
 * è´¨æŠ¼ä»£å¸
 */
const handleStake = async () => {
  if (networkGuard()) return
  const loadingId = showLoading('è´¨æŠ¼å¤„ç†ä¸­...')
  loadingStates.value.stake = true
  try {
    if (stakeAmount.value > myBalance.value) {
      showError(`è´¨æŠ¼æ•°é‡ä¸èƒ½è¶…è¿‡è´¦æˆ·ä½™é¢`)
      return
    }
    if (!stakeAmount.value || stakeAmount.value <= 0) {
      showError('è¯·è¾“å…¥æœ‰æ•ˆçš„è´¨æŠ¼æ•°é‡')
      return
    }

    const tokenContract = getGovernanceTokenContract()
    const requiredAmount = getWeb3().utils.toWei(stakeAmount.value.toString(), 'ether')

    // æ£€æŸ¥æˆæƒ
    const allowance = await tokenContract.methods
        .allowance(account.value, votingContractAddress)
        .call()

    if (Number(allowance) < Number(requiredAmount)) {
      approveStatus.value = 'æˆæƒä¸­...'
      isApproving.value = true
      await tokenContract.methods
          .approve(votingContractAddress, requiredAmount)
          .send({ from: account.value })
      approveStatus.value = `æˆæƒæˆåŠŸ`
    }

    // æ‰§è¡Œè´¨æŠ¼
    const contract = getVotingContract()
    await contract.methods.stake(requiredAmount).send({ from: account.value })
    await loadData()

    // æ›´æ–°é€šçŸ¥
    notifications.value = notifications.value.map(n =>
        n.id === loadingId ? {
          ...n,
          message: `æˆåŠŸè´¨æŠ¼ ${stakeAmount.value} ${tokenSymbol.value}`,
          type: 'success',
          persistent: false
        } : n
    )
  } catch (err) {
    notifications.value = notifications.value.filter(n => n.id !== loadingId)
    handleError(err, isApproving.value ? 'è´¨æŠ¼å¤±è´¥' : 'è´¨æŠ¼å¤±è´¥')
  } finally {
    loadingStates.value.stake = false
    isApproving.value = false
  }
}

/**
 * è§£é™¤è´¨æŠ¼
 */
const unstake = async () => {
  if (networkGuard()) return
  const loadingId = showLoading('è§£é™¤è´¨æŠ¼ä¸­...')
  loadingStates.value.unstake = true
  try {
    // è¾“å…¥éªŒè¯
    if (stakeAmount.value <= 0) {
      showError('è¯·è¾“å…¥æœ‰æ•ˆçš„è§£é™¤è´¨æŠ¼æ•°é‡')
      return
    }
    if (stakeAmount.value > stakedBalance.value) {
      showError(`è§£é™¤è´¨æŠ¼æ•°é‡ä¸èƒ½è¶…è¿‡å·²è´¨æŠ¼æ•°é‡ï¼ˆå½“å‰è´¨æŠ¼ï¼š${stakedBalance.value} ${tokenSymbol.value}ï¼‰`)
      return
    }

    const contract = getVotingContract()
    await contract.methods.unstake(
        getWeb3().utils.toWei(stakeAmount.value.toString(), 'ether')
    ).send({ from: account.value })

    await loadData()

    notifications.value = notifications.value.map(n =>
        n.id === loadingId ? {
          ...n,
          message: `æˆåŠŸè§£é™¤è´¨æŠ¼ ${stakeAmount.value} ${tokenSymbol.value}`,
          type: 'success',
          persistent: false
        } : n
    )
  } catch (err) {
    notifications.value = notifications.value.filter(n => n.id !== loadingId)
    handleError(err, 'è§£é™¤è´¨æŠ¼å¤±è´¥')
  } finally {
