/**
 * åˆ›å»ºææ¡ˆ
 */
const createProposal = async () => {
  if (networkGuard()) return
  const loadingId = showLoading('ææ¡ˆåˆ›å»ºä¸­...')
  loadingStates.value.createProposal = true
  try {
    const contract = getVotingContract()
    const durationSeconds = newProposalDurationHours.value * 3600
    await contract.methods.createProposal(
        newProposalDescription.value,
        durationSeconds
    ).send({ from: account.value })

    showCreateProposalModal.value = false
    await loadData()

    notifications.value = notifications.value.map(n =>
        n.id === loadingId ? {
          ...n,
          message: 'ææ¡ˆåˆ›å»ºæˆåŠŸ',
          type: 'success',
          persistent: false
        } : n
    )
  } catch (err) {
    notifications.value = notifications.value.filter(n => n.id !== loadingId)
    handleError(err, 'ææ¡ˆåˆ›å»ºå¤±è´¥')
  } finally {
    loadingStates.value.createProposal = false
  }
}

/**
 * å‚ä¸æŠ•ç¥¨
 * @param {number} proposalId - ææ¡ˆID
 * @param {boolean} isFor - æ˜¯å¦èµæˆ
 */
const handleVote = async (proposalId, isFor) => {
  const loadingId = showLoading('æäº¤æŠ•ç¥¨ä¸­...')
  loadingStates.value.vote.add(proposalId) // è®°å½•æ­£åœ¨æŠ•ç¥¨çš„ææ¡ˆ
  try {
    if (proposals.value[proposalId].hasVoted) {
      showError('æ‚¨å·²ç»å‚ä¸è¿‡è¯¥ææ¡ˆæŠ•ç¥¨')
      return
    }

    if (stakedBalance.value === 0) {
      showError('è¯·å…ˆè´¨æŠ¼ä»£å¸è·å¾—æŠ•ç¥¨æƒ')
      return
    }

    const contract = getVotingContract()
    await contract.methods.vote(proposalId, isFor).send({ from: account.value })

    // æœ¬åœ°ç«‹å³æ›´æ–°çŠ¶æ€
    proposals.value = proposals.value.map((p, index) => {
      if (index === proposalId) {
        return {
          ...p,
          hasVoted: true,
          forWeight: isFor ? p.forWeight + stakedBalance.value : p.forWeight,
          againstWeight: !isFor ? p.againstWeight + stakedBalance.value : p.againstWeight
        }
      }
      return p
    })

    // æ›´æ–°é€šçŸ¥
    notifications.value = notifications.value.map(n =>
        n.id === loadingId ? {
          ...n,
          message: 'æŠ•ç¥¨æˆåŠŸ',
          type: 'success',
          persistent: false
        } : n
    )
  } catch (err) {
    notifications.value = notifications.value.filter(n => n.id !== loadingId)
    handleError(err, 'æŠ•ç¥¨å¤±è´¥')
  } finally {
    loadingStates.value.vote.delete(proposalId)
  }
}

/**
