const startProposal = async (proposalId) => {
  if (networkGuard()) return
  const loadingId = showLoading('å¯åŠ¨æŠ•ç¥¨ä¸­...')
  loadingStates.value.startProposal.add(proposalId)
  try {
    const contract = getVotingContract()
    await contract.methods.startProposal(proposalId)
