const shortAddress = (addr) => addr ? `${addr.slice(0,6)}...${addr.slice(-4)}` : ''

const weiToEther = (wei) =>
    Number(getWeb3().utils.fromWei(wei.toString(), 'ether'))


function statusBadgeClass(status) {
  return {
    0: 'bg-gray-100 text-gray-600',
    1: 'bg-blue-100 text-blue-600',
    2: 'bg-purple-100 text-purple-600'
  }[status]
}

function proposalStatus(status) {
  return {
    0: 'ç­‰å¾…å¼€å§‹',
    1: 'æŠ•ç¥¨ä¸­',
    2: ''
  }[status]
}

function votePercentage(proposal, isFor) {
  const total = proposal.forWeight + proposal.againstWeight
  return total === 0 ? 0 : ((isFor ? proposal.forWeight : proposal.againstWeight) / total * 100).toFixed(1)
}

async function handleError(err, context) {
  console.error(context, err)
  showError(`${context}: ${err.message || 'æœªçŸ¥é”™è¯¯'}`)
}

// ==================== å®šæ—¶å™¨é€»è¾‘ ====================
const timer = ref(null)
onMounted(() => {
  timer.value = setInterval(() => {
    proposals.value = proposals.value.map(p => {
      const currentTime = Math.floor(Date.now()/1000)
      return {
        ...p,
        status: p.status === 1 && p.endTime < currentTime ? 2 : p.status,
        isOverdue: p.status === 1 && p.endTime < currentTime
      }
    })
  }, 1000)
})

onUnmounted(() => {
  clearInterval(timer.value)
})

// ==================== å‰©ä½™æ—¶é—´æ˜¾ç¤º ====================
function remainingTime(endTime) {
  if (!endTime) return '-'
  const currentTime = Math.floor(Date.now()/1000)
  const remaining = endTime - currentTime

  if (remaining <= 0) return 'å·²ç»“æŸ'

  const hours = Math.floor(remaining / 3600)
  const minutes = Math.floor((remaining % 3600) / 60)
  const seconds = remaining % 60
  return `${hours}æ—¶${minutes}åˆ†${seconds}ç§’`
}
